import axios from 'axios'

export function obter(id) {
    return axios.get('/analise-projeto/participante/obter/' + id)
}

export function obterTodos() {
    return axios.get('/analise-projeto/participante/obter-todos')
}

export function obterTodosPorRelatorio(analiseId) {
    return axios.get('/analise-projeto/participante/obter-todos-relatorio/' + analiseId)
}

export function obterTodosPorProjeto(projetoId) {
    return axios.get('/analise-projeto/participante/obter-todos-projeto/' + projetoId)
}

export function cadastrar(data) {
    return axios.put('/analise-projeto/participante/cadastrar', {
        projetoId: data.projetoId,
        nome: data.nome,
        sobrenome: data.sobrenome,
        email: data.email,
        empresa: data.empresa
    })
}

export function editar(data) {
    return axios.post('/analise-projeto/participante/editar/' + data.id, {
        nome: data.nome,
        sobrenome: data.sobrenome,
        email: data.email,
        empresa: data.empresa
    })
}

export function vincular(data) {
    return axios.post('/analise-projeto/participante/vincular/' + data.analiseId + '/' + data.participanteId, {
        responsavel: data.responsavel
    })
}

export function desvincular(analiseId, participanteId) {
    return axios.post('/analise-projeto/participante/desvincular/' + analiseId + '/' + participanteId)
}


