import React from 'react'

import { Grid, Card, List, Button, Segment, Table, Icon, Image, Menu, Popup } from 'semantic-ui-react'

import Loader from './../../../../../components/loader'
import Relatorio from './relatorio'
import Itens from './itens'
import Participante from './participante'
import Arquivo from './arquivo'

import DialogAnalise from './../../../containers/dialog-analise/cadastro.container'
import DialogExcluirAnalise from './../../../containers/dialog-analise/excluir.container'
import DialogItem from './../../../containers/dialog-item/cadastrar.container'

export default (props) => {

    const {
        carregando, onDesvincular, onAprovar, onDeletar, onAlterarPosicao, showArquivoResult, showArquivos, showParticipanteResult, itens, showParticipantes, activeIndex, handleClick, assuntos, organizados,
    } = props

    return (
        <Loader loading={carregando}>
            <Grid container>

                <Grid.Row>
                    <Grid.Column>
                        <Menu>
                            <Menu.Item>{props.analise.titulo}</Menu.Item>
                            <DialogAnalise id={props.analise._id}>
                                <Menu.Item onClick={() => { }}><Icon name='edit' /> Editar</Menu.Item>
                            </DialogAnalise>
                            <Popup
                                key="participante"
                                position="bottom center"
                                content="Ao clicar enviará e-mail para os participantes adicionados, informando os detalhes do relatório e os itens com as replicas não aprovadas."
                                trigger={
                                    <Menu.Item onClick={() => { }}><Icon name='mail forward' /> Emitir relatório</Menu.Item>
                                } />
                            <DialogItem projetoId={props.projeto} analiseId={props.analise._id}>
                                <Menu.Item icon="plus" content="Adicionar item" />
                            </DialogItem>
                            <Menu.Menu position='right'>
                                <DialogExcluirAnalise id={props.analise._id} projetoId={props.projeto}>
                                    <Menu.Item onClick={() => { }}><Icon name='trash' /> Excluir</Menu.Item>
                                </DialogExcluirAnalise>
                            </Menu.Menu>
                        </Menu>
                    </Grid.Column>
                </Grid.Row>

                <Grid.Row>
                    <Grid.Column width={4}>
                        <Relatorio
                            analise={props.analise}
                        />
                        <br />
                        <Participante
                            onDesvincular={onDesvincular}
                            participantes={props.participantes}
                            participante={props.participante}
                            analise={props.analise}
                            projeto={props.projeto}
                            showParticipanteResult={showParticipanteResult}
                            showParticipantes={showParticipantes}
                        />
                        <br />
                        <Arquivo
                            analise={props.analise}
                            showArquivoResult={showArquivoResult}
                            showArquivos={showArquivos}
                        />
                    </Grid.Column>

                    <Grid.Column width={12}>
                        <Itens
                            onAprovar={onAprovar}
                            onDeletar={onDeletar}
                            onAlterarPosicao={onAlterarPosicao}
                            handleClick={handleClick}
                            activeIndex={activeIndex}
                            assuntos={assuntos}
                            organizados={organizados}
                            analise={props.analise}
                            projeto={props.projeto}
                        />
                    </Grid.Column>
                </Grid.Row>
            </Grid>
        </Loader>
    )
}


